import React from "react";
import { Close } from "grommet-icons";
import {
  Layer,
  Box,
} from "grommet";

const Modal = ({ children, onClose }) => {
  return (
        <Layer>
          <Box overflow="scroll" pad="medium" margin={{ left: 'auto' }}>
            <Close 
            style={{ cursor: 'pointer' }}
            color='plain' 
            onClick={() => onClose && onClose()} />
            {children}
          </Box>
        </Layer>
  );
};

export default Modal;
